import React, { useState, useEffect } from 'react';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider';
import FormControl from '@material-ui/core/FormControl';
import {
  ThemeProvider,
  createMuiTheme,
  makeStyles,
} from '@material-ui/core/styles';
import { ItemInput } from './Components/ItemInput';
import { EnchantmentInput } from './Components/EnchantmentInput';
import { SoulInput } from './Components/SoulInput';
import { PerksInput } from './Components/PerksInput';
import { EnchantmentEffect } from './Components/EnchantmentEffect';
import Perk from './Classes/Perk';
import Enchantment from './Classes/Enchantment';
import WeaponEnchantment from './Classes/WeaponEnchantment';

const theme = createMuiTheme({
  palette: {
    type: 'dark',
  },
});

const useStyles = makeStyles((theme) => ({
  app: { textAlign: 'center' },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  paperContainer: { padding: 16, width: '80%', maxWidth: '1080px' },
  appHeader: {
    backgroundColor: '#282c34',
    minHeight: '100vh',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    fontSize: 'calc(10px + 2vmin)',
  },
  slider: { width: '80%' },
}));

function App() {
  const otherTreePerks = [
    new Perk('Augmented Flames', { level: 0, maxLevel: 2 }),
    new Perk('Augmented Frost', { level: 0, maxLevel: 2 }),
    new Perk('Augmented Shock', { level: 0, maxLevel: 2 }),
    new Perk('Necromage', { selected: false }),
    new Perk('Master of the Mind', { selected: false }),
  ];
  const enchantmentPerks = [
    new Perk('Enchanter', { level: 0, maxLevel: 5 }),
    new Perk('Fire Enchanter', { selected: false }),
    new Perk('Frost Enchanter', { selected: false }),
    new Perk('Storm Enchanter', { selected: false }),
    new Perk('Insightful Enchanter', { selected: false }),
    new Perk('Corpus Enchanter', { selected: false }),
    new Perk('Extra Effect', { selected: false }),
    ...otherTreePerks,
  ].sort((a, b) => {
    return a.maxLevel ? -1 : 1;
  });

  const weaponEnchantments = [
    new WeaponEnchantment('Absorb Health', 8),
    new WeaponEnchantment('Absorb Magicka', 10),
    new WeaponEnchantment('Absorb Stamina', 10),
    new WeaponEnchantment('Chaos Damage', 10),
    new WeaponEnchantment('Fire Damage', 10),
    new WeaponEnchantment('Frost Damage', 10), //damage and stamina damage 2s(fixed)
    new WeaponEnchantment('Magicka Damage', 15),
    new WeaponEnchantment('Shock Damage', 10), // and 50% Magicka Damage
    new WeaponEnchantment('Stamina Damage', 15),
    new WeaponEnchantment('Banish', 10),
    new WeaponEnchantment('Fear', 10), // 30s
    new WeaponEnchantment('Turn Undead', 10), // 30s
    new WeaponEnchantment('Paralyze', 2, false), //26% fixed, 2s(noSoul)
    new WeaponEnchantment('Soul Trap', 4, false), //seconds (noSoul)
    new WeaponEnchantment('Fiery Soul Trap', 5, false), //fixed, 10s(noSoul)
    new WeaponEnchantment("Huntsman's Prowess", 3), //vs animals
    new WeaponEnchantment('Silent Moons Enchant', 10), // between 2100 and 0500
    new WeaponEnchantment('Smithing Expertise', 5), //shock damage, +5 Smithing(fixed)
  ];
  const fortifySkillEnchantments = [
    new Enchantment('Fortify Alchemy', ['Head', 'Neck', 'Hands', 'Finger'], 8),
    new Enchantment(
      'Fortify Alteration',
      ['Head', 'Neck', 'Chest', 'Finger'],
      8
    ),
    new Enchantment('Fortify Archery', ['Head', 'Neck', 'Hands', 'Finger'], 13),
    new Enchantment('Fortify Barter', ['Neck'], 8),
    new Enchantment('Fortify Block', ['Neck', 'Hands', 'Finger', 'Shield'], 13),
    new Enchantment(
      'Fortify Conjuration',
      ['Head', 'Neck', 'Chest', 'Finger'],
      8
    ),
    new Enchantment(
      'Fortify Destruction',
      ['Head', 'Neck', 'Chest', 'Finger'],
      8
    ),
    new Enchantment(
      'Fortify Heavy Armor',
      ['Neck', 'Chest', 'Hands', 'Finger'],
      8
    ),
    new Enchantment('Fortify Illusion', ['Head', 'Neck', 'Chest', 'Finger'], 8),
    new Enchantment(
      'Fortify Light armor',
      ['Neck', 'Chest', 'Hands', 'Finger'],
      8
    ),
    new Enchantment(
      'Fortify Lockpicking',
      ['Head', 'Neck', 'Hands', 'Finger'],
      8
    ),
    new Enchantment(
      'Fortify One-Handed',
      ['Neck', 'Hands', 'Finger', 'Feet'],
      13
    ),
    new Enchantment(
      'Fortify Pickpocket',
      ['Neck', 'Hands', 'Finger', 'Feet'],
      13
    ),
    new Enchantment(
      'Fortify Restoration',
      ['Head', 'Neck', 'Chest', 'Finger'],
      8
    ),
    new Enchantment(
      'Fortify Smithing',
      ['Neck', 'Chest', 'Hands', 'Finger'],
      8
    ),
    new Enchantment('Fortify Sneak', ['Neck', 'Hands', 'Finger', 'Feet'], 13),
    new Enchantment(
      'Fortify Two-Handed',
      ['Neck', 'Hands', 'Finger', 'Feet'],
      13
    ),
  ];
  const fortifyAttributeEnchantments = [
    new Enchantment('Fortify Healing Rate', ['Finger', 'Neck', 'Chest'], 20),
    new Enchantment(
      'Fortify Health',
      ['Finger', 'Neck', 'Chest', 'Shield'],
      20
    ),
    new Enchantment('Fortify Magicka', ['Finger', 'Head', 'Neck', 'Hands'], 20),
    new Enchantment('Fortify Magicka Regen', ['Finger', 'Head', 'Chest'], 20),
    new Enchantment('Fortify Stamina', ['Finger', 'Neck', 'Chest', 'Feet'], 20),
    new Enchantment(
      'Fortify Stamina Regen',
      ['Neck', 'Chest', 'Feet', 'Finger'],
      10
    ),
  ];
  const fortifyOtherEnchantments = [
    new Enchantment(
      'Fortify Carry Weight',
      ['Neck', 'Hands', 'Finger', 'Feet'],
      15
    ),
    new Enchantment('Fortify Alteration and Magicka Regen', ['Chest'], 5), // +10% Magicka regen
    new Enchantment('Fortify Conjuration and Magicka Regen', ['Chest'], 5), // +10% Magicka regen
    new Enchantment('Fortify Destruction and Magicka Regen', ['Chest'], 5), // +10% Magicka regen
    new Enchantment('Fortify Illusion and Magicka Regen', ['Chest'], 5), // +10% Magicka regen
    new Enchantment('Fortify Restoration and Magicka Regen', ['Chest'], 5), // +10% Magicka regen
    new Enchantment('Fortify Unarmed', ['Hands', 'Finger'], 5),
  ];
  const resistEnchantments = [
    new Enchantment(
      'Resist Disease',
      ['Neck', 'Finger', 'Shield', 'Chest'],
      25
    ),
    new Enchantment('Resist Fire', ['Neck', 'Finger', 'Shield', 'Feet'], 15),
    new Enchantment('Resist Frost', ['Neck', 'Finger', 'Shield', 'Feet'], 15),
    new Enchantment('Resist Magic', ['Neck', 'Finger', 'Shield'], 8),
    new Enchantment('Resist Poison', ['Neck', 'Finger', 'Shield', 'Chest'], 15),
    new Enchantment('Resist Shock', ['Neck', 'Finger', 'Shield', 'Feet'], 15),
  ];
  const otherEnchantments = [
    new Enchantment('Muffle', ['Feet']),
    new Enchantment('Waterbreathing', ['Head', 'Neck', 'Finger']),
  ];
  const enchantments = [
    ...fortifySkillEnchantments,
    ...fortifyAttributeEnchantments,
    ...fortifyOtherEnchantments,
    ...resistEnchantments,
    ...otherEnchantments,
    ...weaponEnchantments,
  ];

  const classes = useStyles();
  const [state, setState] = useState({
    slot: '',
    enchantment: [''],
    enchantments: enchantments,
    soulSize: '',
    perks: enchantmentPerks,
    skill: '',
  });

  useEffect(() => {
    if (process.env.NODE_ENV === 'development') console.log(state);
  });

  const handleChangeSlot = (event) => {
    setState({ ...state, slot: event.target.value });
  };
  const handleChangeFirstEnchantment = (event) => {
    const targetedEnchantmentName = event.target.value;
    const targetedEnchantment = enchantments.find((enchantment) => {
      return enchantment.name === targetedEnchantmentName;
    });
    setState({
      ...state,
      enchantment: [targetedEnchantment, state.enchantment[1]],
    });
  };
  const handleChangeSecondEnchantment = (event) => {
    const targetedEnchantmentName = event.target.value;
    const targetedEnchantment = enchantments.find((enchantment) => {
      return enchantment.name === targetedEnchantmentName;
    });
    setState({
      ...state,
      enchantment: [state.enchantment[0], targetedEnchantment],
    });
  };
  const handleChangeSoulSize = (event) => {
    setState({ ...state, soulSize: event.target.value });
  };
  const handleChangePerk = (event) => {
    const value = JSON.parse(event.target.value);
    setState({
      ...state,
      perks: state.perks.map((perk) => {
        if (perk.name === value.name) {
          perk.toggleSelected();
        }
        if ((value, value.name === 'Extra Effect' && value.selected)) {
          setState({
            ...state,
            enchantment: state.enchantment.slice(0, 1),
          });
        }
        return perk;
      }),
    });
  };
  const handleChangeMultilevelPerk = (event) => {
    const value = JSON.parse(event.target.value);
    setState({
      ...state,
      perks: state.perks.map((perk) => {
        if (perk.name === value.name) {
          perk.level = value.level;
        }
        return perk;
      }),
    });
  };

  const handleChangeSkill = (event) => {
    setState({ ...state, skill: event.target.value });
  };

  return (
    <ThemeProvider theme={theme}>
      <div className='App'>
        <header className={classes.appHeader}>
          <Paper className={classes.paperContainer}>
            <ItemInput
              classes={classes}
              state={state}
              handleChangeSlot={handleChangeSlot}
            />
            <EnchantmentInput
              classes={classes}
              state={state}
              handleChangeFirstEnchantment={handleChangeFirstEnchantment}
              handleChangeSecondEnchantment={handleChangeSecondEnchantment}
            />
            <SoulInput
              classes={classes}
              state={state}
              handleChangeSoulSize={handleChangeSoulSize}
            />
            <FormControl className={classes.formControl}>
              <TextField
                id='standard-basic'
                label='Skill Level'
                type='number'
                value={state.skill}
                onChange={handleChangeSkill}
                error={state.skill > 100 || state.skill < 0}
                helperText={
                  state.skill > 100 || state.skill < 0
                    ? 'Skill level must be between 0 and 100'
                    : ''
                }
              />
            </FormControl>
            <Divider />
            <PerksInput
              classes={classes}
              state={state}
              handleChangePerk={handleChangePerk}
              handleChangeMultilevelPerk={handleChangeMultilevelPerk}
            />
            <Divider />
            <EnchantmentEffect classes={classes} state={state} />
          </Paper>
        </header>
      </div>
    </ThemeProvider>
  );
}

export default App;
