import Enchantment from './Enchantment';

export default class WeaponEnchantment extends Enchantment {
  constructor(name, baseMagnitude = 1, useSouls = true) {
    super(name, ['Weapon'], baseMagnitude);
    this.useSouls = useSouls;
  }

  getSoulMultiplier(soulSize) {
    switch (soulSize) {
      case 'Grand' || !this.useSouls:
        return 1;
      case 'Greater':
        return 2 / 3;
      case 'Common':
        return 1 / 3;
      case 'Lesser':
        return 1 / 6;
      case 'Petty':
        return 1 / 12;
      default:
        return 0;
    }
  }
}
