import Typography from '@material-ui/core/Typography';
import Enchantment from '../Classes/Enchantment';

export const EnchantmentEffect = (props) => {
  const { state } = props;

  return (
    <>
      <Typography color='textSecondary' display='block' variant='caption'>
        Effect
      </Typography>
      {state.enchantment.map((enchantment) => {
        return enchantment && enchantment instanceof Enchantment ? (
          <Typography key={enchantment.name + 'Typography'}>
            {enchantment.getDescription(state)}
          </Typography>
        ) : null;
      })}
    </>
  );
};
