import React from 'react';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

export const EnchantmentInput = (props) => {
  const {
    classes,
    state,
    handleChangeFirstEnchantment,
    handleChangeSecondEnchantment,
  } = props;
  const { enchantments } = state;

  return (
    <>
      <FormControl className={classes.formControl}>
        <InputLabel id='demo-simple-select-filled-label'>
          Enchantment
        </InputLabel>
        <Select
          labelId='demo-simple-select-filled-label'
          id='demo-simple-select-filled'
          value={state.enchantment[0] ? state.enchantment[0].name : ''}
          onChange={handleChangeFirstEnchantment}
        >
          <MenuItem value=''>
            <em>None</em>
          </MenuItem>
          {enchantments
            .filter((enchantment) => {
              return (
                enchantment.slots.includes(state.slot) &&
                enchantment.name !== state.enchantment[1]?.name
              );
            })
            .map((enchantment) => {
              return (
                <MenuItem
                  key={enchantment.name}
                  value={enchantment.name}
                >{`${enchantment.name}`}</MenuItem>
              );
            })}
        </Select>
      </FormControl>
      {state.perks.reduce((isExtraEffectSelected, perk) => {
        return isExtraEffectSelected
          ? isExtraEffectSelected
          : perk.name === 'Extra Effect' && perk.selected;
      }, false) ? (
        <FormControl className={classes.formControl}>
          <InputLabel id='demo-simple-select-filled-label'>
            Enchantment 2
          </InputLabel>
          <Select
            labelId='demo-simple-select-filled-label'
            id='demo-simple-select-filled'
            data-enchantment={state.enchantment[1]}
            value={state.enchantment[1] ? state.enchantment[1].name : ''}
            onChange={handleChangeSecondEnchantment}
          >
            <MenuItem value=''>
              <em>None</em>
            </MenuItem>
            {enchantments
              .filter((enchantment) => {
                return (
                  enchantment.slots.includes(state.slot) &&
                  enchantment.name !== state.enchantment[0]?.name
                );
              })
              .map((enchantment) => {
                return (
                  <MenuItem
                    key={enchantment.name}
                    value={enchantment.name}
                  >{`${enchantment.name}`}</MenuItem>
                );
              })}
          </Select>
        </FormControl>
      ) : null}
    </>
  );
};
