export default class Enchantment {
  constructor(name, slots, baseMagnitude = 1) {
    this.name = name;
    this.slots = slots;
    this.baseMagnitude = baseMagnitude;
  }
  getSkillMultiplier(skill, potionEffect = 0) {
    return (
      1 +
      (((skill * (1 + potionEffect)) / 100) *
        ((skill * (1 + potionEffect)) / 100 - 0.14)) /
        3.4
    );
  }

  getSoulMultiplier(soulSize) {
    switch (soulSize) {
      case 'Grand':
        return 1;
      case 'Greater':
        return 2 / 3;
      case 'Common':
        return 1 / 3;
      case 'Lesser':
        return 1 / 6;
      case 'Petty':
        return 1 / 12;
      default:
        return 0;
    }
  }

  getPerkMultipliers(perks) {
    return perks
      .filter((perk) => {
        return perk.selected || perk.level;
      })
      .map((perk) => {
        return perk.multiplier;
      })
      .reduce((totalMultiplier, perkMultiplier) => {
        return totalMultiplier * perkMultiplier;
      }, 1);
  }

  getDescription(state) {
    const { soulSize, perks, skill } = state;
    const skillMultiplier = this.getSkillMultiplier(skill);
    const soulMultiplier = this.getSoulMultiplier(soulSize);
    const perkMultiplier = this.getPerkMultipliers(perks);

    return `${this.name}: ${Math.floor(
      this.baseMagnitude * skillMultiplier * soulMultiplier * perkMultiplier
    )}`;
  }
}
